@import url("https://fonts.googleapis.com/css2?family=Inter:wght@800&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.app .bg-image {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: -1;
  background-image: linear-gradient(
      115deg,
      rgb(3 33 49 / 50%),
      rgb(0 0 0 / 50%)
    ),
    url(./images/wp2530395-kanye-west-2018-wallpapers.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

.head > h1 {
  font-family: "Inter", sans-serif;
  color: rgb(102 185 247);
  font-size: 60px;
  text-align: center;
}
.content {
  padding-top: 100px;
  align-content: center;
  display: flex;
  flex-direction: column;
}
.content p {
  text-align: center;
  color: white;
  font-family: "Inter", sans-serif;
  font-size: 30px;
  padding-bottom: 70px;
}

.butt {
  display: flex;
  align-content: center;
  justify-content: center;
}
.butt button {
  margin: auto;
  padding: 15px 20px;
  color: black;
  background-color: rgb(102 185 247);
  outline: none;
  border-radius: 9px;
  align-content: center;
  justify-content: center;
  position: fixed;
  font-family: "Inter", sans-serif;
  font-size: 17px;
}
